@import "./../base/utility";

$dark: #23282d;
$dark-2: #7b7d8b;
$dark-3: #272b41;
$primary: #3e62f5;
$success: #08bf9c;
$info: #2c99ff;
$warning: #fa8b0c;
$pink: #ff408c;
$nav_link_color: #7a8289;
$nav_link_hover_color: $dark;
$nav_icon_color: $primary;
$gray: #eff0f3;
$light-gray: #f4f5f7;

#atbdp_cpt_options_metabox {
  .inside {
    margin: 0;
    padding: 0;
  }
  .postbox-header {
    display: none;
  }
}

.atbdp-cpt-manager {
  display: block;
  color: $dark;
  background: #fff;
  padding: 30px;
}

.atbdp-cptm-header {
  display: block;
}

.atbdp-cptm-body {
  display: block;
}

.atbdp-cptm-footer {
  display: block;
  padding: 20px 10px;
  background: #fff;
}

.atbdp-cptm-footer-actions {
  display: flex;
  justify-content: center;
  align-content: center;
  flex-wrap: wrap;
}

.cptm-btn {
  margin: 0 5px;
  border: 1px solid;
  padding: 10px 20px;
  color: #fff;
  border-radius: 5px;
  cursor: pointer;
  transition: all ease-in-out 300ms;
  &:disabled {
    cursor: not-allowed;
    opacity: 0.5;
  }
}

.cptm-btn-primary {
  color: #fff;
  border-color: $primary;
  background-color: $primary;
  &:hover {
    background-color: darken($primary, $amount: 2);
  }
}

.cptm-btn-secondery {
  color: $primary;
  border-color: $primary;
  background-color: transparent;
  &:hover {
    color: #fff;
    background-color: $primary;
  }
}

.cptm-form-builder-group-field-drop-area {
  display: block;
  padding: 20px;
  text-align: center;
  background-color: #fff;
  transition: all ease-in-out 300ms;
  border: 1px dashed #c0c0c0;
  &.drag-enter {
    background-color: #c6c6c6;
  }
}
.cptm-form-builder-group-field-drop-area-label {
  margin: 0;
  pointer-events: none;
}

.atbdp-cptm-status-feedback {
  margin: 20px 0;
}

.atbdp-cptm-alert {
  margin: 5px auto;
  max-width: 500px;
  padding: 10px 20px;
  text-align: center;
  color: #fff;
  background-color: $primary;
  border-radius: 5px;
}

.cptm-alert-success {
  color: #fff;
  background-color: $primary;
}
.cptm-alert-error {
  color: #fff;
  background-color: rgb(197, 22, 22);
}

// ====================
// Header Navigation
// ====================
.cptm-header-navigation {
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  background-color: #f4f5f7;
  border-radius: 6px 6px 0 0;
}

.cptm-header-nav__list-item {
  margin: 0;
  display: inline-block;
  list-style: none;
  text-align: center;
  padding: 0 25px;
}

.cptm-header-nav__list-item-link {
  @extend .reset-pseudo-link;
  display: block;
  padding: 45px 0;
  text-decoration: none;
  border-right: 1px solid #f2f2f2;
  transition: all ease-in-out 300ms;
  background-color: #f4f5f7;
  position: relative;
  border-bottom: 2px solid transparent;

  &.active {
    border-bottom-color: $primary;
    .cptm-header-nav__icon {
      color: #fff;
      background-color: $primary;
    }
    .cptm-header-nav__label {
      color: $primary;
    }
  }
}

.cptm-header-nav__icon {
  display: inline-flex;
  margin-bottom: 15px;
  min-width: 44px;
  min-height: 44px;
  text-align: center;
  font-size: 18px;
  text-align: center;
  border-radius: 50%;
  background: rgba($primary, 0.1);
  color: $primary;
  transition: all ease-in-out 300ms;
  align-items: center;
  justify-content: center;
}
.general {
  .cptm-header-nav__icon {
    background: rgba($primary, 0.1);
    color: $primary;
  }
  .active {
    .cptm-header-nav__icon {
      background-color: $primary;
    }
  }
}
.submission_form {
  .cptm-header-nav__icon {
    background: rgba($success, 0.1);
    color: $success;
  }
  .active {
    .cptm-header-nav__icon {
      background-color: $success;
    }
  }
}
.single_page_layout {
  .cptm-header-nav__icon {
    background: rgba($warning, 0.1);
    color: $warning;
  }
  .active {
    .cptm-header-nav__icon {
      background-color: $warning;
    }
  }
}
.listings_card_layout {
  .cptm-header-nav__icon {
    background: rgba($info, 0.1);
    color: $info;
  }
  .active {
    .cptm-header-nav__icon {
      background-color: $info;
    }
  }
}
.search_forms {
  .cptm-header-nav__icon {
    background: rgba($pink, 0.1);
    color: $pink;
  }
  .active {
    .cptm-header-nav__icon {
      background-color: $pink;
    }
  }
}

.cptm-header-nav__label {
  display: block;
  font-weight: bold;
  color: #5a5f7d;
  transition: all ease-in-out 300ms;
  font-size: 15px;
}

// ====================
// Sub Tabs
// ====================

.cptm-title-area {
  width: 510px;
  margin: 0 auto 40px;
}
.submission-form {
  .cptm-title-area {
    width: 100%;
  }
}

.cptm-mb-60 {
  margin-bottom: 60px;
}

.cptm-title {
  margin: 0;
  font-size: 24px;
  font-weight: bold;
  color: $dark;
}

.atbdp-cptm-tab-contents {
  display: block;
  background-color: #fff;
}

.atbdp-cptm-tab-item {
  display: none;
  &.active {
    display: block;
  }
}

.cptm-tab-content-header {
  background-color: #f8f9fb;
}
.cptm-tab-content-body {
  display: block;
}

.cptm-tab-content {
  margin: 0 auto;
  padding: 60px 75px;
  min-height: 100px;
  max-width: 700px;
  box-sizing: border-box;
  &.tab-full-width {
    max-width: 100%;
  }
}

.cptm-tab-sub-content-item {
  margin: 0 auto;
  display: none;
  &.active {
    display: block;
  }
}

.cptm-row {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -15px;
}
.cptm-col-6 {
  flex-grow: 1;
  width: calc(50% - 30px);
  padding: 0 15px;
}

.cptm-form-builder-preset-fields:not(:last-child) {
  margin-bottom: 50px;
}
.cptm-title-3 {
  margin: 0;
  color: $dark-3;
  flex-grow: 1;
  font-weight: 500;
  font-size: 18px;
}

.cptm-description-text {
  margin: 5px 0 20px;
  color: #5a5f7d;
  font-size: 15px;
}

.cptm-form-builder-active-fields {
  display: block;
}

.cptm-form-builder-active-fields-container {
  background-color: #f4f5f7;
  padding: 40px;
  border-radius: 6px;
  // max-height: 100vh;
  // overflow: hidden;
  // overflow-y: auto;
}

.cptm-form-builder-active-fields-footer {
  text-align: center;
}

.cptm-form-builder-active-fields-group {
  position: relative;
  margin-bottom: 40px;
}

.cptm-form-builder-group-header-section {
  margin-bottom: 20px;
}

.cptm-form-builder-group-header {
  display: flex;
  align-items: center;
}

.cptm-form-builder-group-title-area {
  display: flex;
  flex-grow: 1;
  padding: 15px 20px;
  margin-right: 25px;
  box-sizing: border-box;
  cursor: move;
  border-radius: 4px;
  background-color: #fff;
  box-shadow: 0px 1px 5px rgba(#a1a8c6, 0.1);
}

.cptm-form-builder-group-title-actions {
  display: block;
}

.cptm-form-builder-group-actions {
  display: block;
  min-width: 25px;
}

.cptm-form-builder-group-title {
  flex-grow: 1;
  margin: 0;
  font-size: 14px;
  color: $dark-3;
}

.cptm-form-builder-group-options {
  background-color: #fff;
  margin: -5px 50px 0 0;
  padding: 22px 25px 25px;
  box-shadow: 0 1px 5px rgba(#a1a8c6, 0.1);
}

.cptm-form-builder-group-fields {
  display: block;
}

.cptm-form-builder-group-field-item {
  margin-bottom: 20px;
  position: relative;
  margin-right: 50px;
  background-color: #fff;
  border-radius: 4px;
}

.cptm-form-builder-group-field-item-actions {
  display: block;
  position: absolute;
  right: -15px;
  transform: translate(34px, 7px);
}

.cptm-form-builder-group-field-item-action-link {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  background-color: #e3e6ef;
  border-radius: 50%;
  width: 34px;
  height: 34px;
  text-align: center;
  color: #868eae;
  transition: all ease-in-out 300ms;
}
.action-trash:hover {
  color: #e62626;
  background-color: rgba(255, 0, 0, 0.15);
}

.cptm-form-builder-group-field-item-header-actions {
  margin-left: 10px;
}

.cptm-form-builder-header-action-link {
  display: inline-block;
  padding: 0;
  font-size: 16px;
  transition: all ease-in-out 300ms;
  @extend .reset-pseudo-link;
}

.action-collapse-up span {
  transition: all ease-in-out 300ms;
  transform: rotate(0);
}

.action-collapse-down span {
  transition: all ease-in-out 300ms;
  transform: rotate(180deg);
}

.cptm-form-builder-group-field-item-header {
  display: flex;
  justify-content: space-between;
  padding: 15px 20px;
  cursor: move;
  box-shadow: 0px 1px 5px rgba(#a1a8c6, 0.1);
  border-radius: 4px;
  .cptm-title-3 {
    font-size: 14px;
    color: $dark-3;
  }
}

.cptm-form-builder-group-field-item-body {
  padding: 22px 25px 25px;
}

.cptm-form-builder-field-list {
  margin: -5px -5px -5px -5px;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
}
.cptm-form-builder-field-list-item {
  list-style: none;
  width: calc(50% - 10px);
  margin: 5px;
  background-color: #f4f5f7;
  padding: 14px 20px;
  box-sizing: border-box;
  cursor: move;
  border-radius: 4px;
  display: flex;
  align-items: center;
}
.cptm-form-builder-field-list-icon {
  display: inline-block;
  width: 28px;
  font-size: 18px;
  color: $dark-3;
}
.cptm-form-builder-field-list-label {
  display: inline-block;
  font-size: 14px;
  font-weight: 500;
  color: $dark-3;
}

.cptm-mb-0 {
  margin-bottom: 0 !important;
}

.cptm-item-footer-drop-area {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 20px;
  transition: all ease-in-out 300ms;
  transform: translate(0, 100%);
  z-index: 5;
  &.drag-enter {
    background-color: rgba(23, 135, 255, 0.3);
  }

  &.cptm-group-item-drop-area {
    height: 40px;
  }
}

.cptm-form-builder-group-field-item-drop-area {
  height: 20px;
  position: absolute;
  bottom: -20px;
  z-index: 5;
  width: 100%;
  transition: all ease-in-out 300ms;
  &.drag-enter {
    background-color: rgba(23, 135, 255, 0.3);
  }
}

// ====================
// Modules
// ====================
// cptm-sub-navigation
.cptm-sub-navigation {
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.cptm-sub-nav__item {
  list-style: none;
  margin: 0;
}

.cptm-sub-nav__item-link {
  @extend .reset-pseudo-link;
  display: block;
  text-decoration: none;
  padding: 12px 25px;
  border-radius: 30px;
  margin: 22px 5px;
  color: #7a8289;
  font-weight: 600;
  background: #f1f2f6;
  transition: 0.3s ease;
  &:hover,
  &.active {
    font-weight: 600;
    color: #fff;
    background: $primary;
  }
  &.active {
    font-weight: bold;
  }
}

// Builder Section
.cptm-builder-section {
  display: flex;
  flex-wrap: wrap;
  position: relative;
}
.cptm-options-area {
  width: 300px;
  perspective: 800px;
  position: absolute;
  top: 0;
  left: 0;
}

.cptm-option-card {
  display: none;
  transition: all linear 300ms;
  transform-origin: center;
  opacity: 0;
  border-radius: 5px;
  text-align: left;
  pointer-events: none;

  &.cptm-animation-flip {
    transform: rotate3d(0, 1, 0, 45deg);
  }
  &.cptm-animation-slide-up {
    transform: translate(0, 30px);
  }

  &.active {
    display: block;
    opacity: 1;
    pointer-events: all;

    &.cptm-animation-flip {
      transform: rotate3d(0, 0, 0, 0deg);
    }
    &.cptm-animation-slide-up {
      transform: translate(0, 0);
    }
  }
}

.cptm-anchor-down {
  display: block;
  text-align: center;
  position: relative;
  top: -1px;
  &:after {
    content: "";
    display: inline-block;
    width: 0;
    height: 0;
    border-left: 15px solid transparent;
    border-right: 15px solid transparent;
    border-top: 15px solid #fff;
  }
}

.cptm-header-action-link {
  display: inline-block;
  padding: 0 10px;
  text-decoration: none;
  color: darken($primary, 30);
  transition: all ease-in-out 300ms;
  @extend .reset-pseudo-link;
  &:hover {
    color: #fff;
  }
}

.cptm-option-card-header {
  border-radius: 5px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
  background-color: $primary;
}
.cptm-option-card-header-title-section {
  display: flex;
  color: #fff;
  padding: 10px;
}
.cptm-option-card-header-title {
  flex-grow: 1;
  margin: 0;
  text-align: left;
  color: #fff;
}
.cptm-header-action-area {
  display: flex;
  padding: 0 0 0 10px;
  justify-content: center;
  align-items: center;
}
.cptm-option-card-header-nav-section {
  display: block;
}

.cptm-option-card-header-nav {
  display: flex;
  color: #fff;
  flex-wrap: wrap;
  padding: 0;
  margin: 0;
  background-color: rgba(255, 255, 255, 0.15);
}
.cptm-option-card-header-nav-item {
  display: block;
  flex-grow: 1;
  text-align: center;
  padding: 8px 10px;
  cursor: pointer;
  margin-bottom: 0;
  transition: all ease-in-out 300ms;
  &.active {
    background-color: rgba(255, 255, 255, 0.15);
  }
}
.cptm-option-card-body {
  padding: 10px;
  border-radius: 5px;
  border-top-right-radius: 0;
  border-top-left-radius: 0;
  box-shadow: 0 10px 40px 0 rgba(0, 0, 0, 0.2);
  background-color: #fff;
}

.cptm-widget-insert-area {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 50%);
}

.cptm-widget-insert-wrap {
  position: relative;
}

.cptm-widget-insert-modal-container {
  position: absolute;
  left: 50%;
  top: 0;
  width: 300px;
  transform: translate(-50%, -100%);
  pointer-events: none;
  &.active {
    pointer-events: all;
  }
}

.cptm-widget-insert-link {
  display: inline-block;
  width: 26px;
  height: 26px;
  line-height: 25px;
  border-radius: 50%;
  font-size: 14px;
  text-align: center;
  text-decoration: none;
  background-color: #fff;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
  color: #5a5f7d;
}
.cptm-listing-card-preview-body,
.cptm-listing-card-preview-footer {
  .cptm-widget-insert-link {
    background: $dark;
    color: #fff;
  }
}

// Widgets
.cptm-widget-badge {
  display: inline-block;
  text-align: center;
  color: #fff;
  border-radius: 5px;
  font-size: 11px;
  padding: 2px 8px;
  margin: 2px;
  font-weight: 700;
  background-color: rgba(255, 255, 255, 0.2);
  transition: 0.3s ease;
  position: relative;
  &:before {
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    left: -8px;
    top: -8px;
    border: 3px solid #2c99ff;
    padding: 5px;
    visibility: hidden;
    opacity: 0;
    transition: 0.3s ease;
  }
  &:hover {
    &:before {
      visibility: visible;
      opacity: 1;
    }
    .cptm-widget-badge-tools {
      visibility: visible;
      opacity: 1;
      pointer-events: all;
    }
  }
}
.cptm-widget-badge-tools {
  position: absolute;
  width: 100%;
  background: #2c99ff;
  left: -8px;
  top: -40px;
  padding: 5px;
  border: 3px solid #2c99ff;
  border-radius: 1px 1px 0 0;
  display: flex;
  align-items: center;
  justify-content: center;
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  transition: 0.3s ease;
  a {
    padding: 0 7px;
    font-size: 14px;
    color: #fff;
  }
}

.cptm-preview-area {
  flex-grow: 1;
  padding: 0 20px;
}

.cptm-placeholder-blcok {
  padding: 2px;
  position: relative;
  min-height: 54px;
  text-align: center;
  border: 2px dashed rgba(255, 255, 255, 0.15);
  transition: all ease-in-out 300ms;
  box-sizing: border-box;
  border-radius: 4px;
  z-index: 99;
  &:hover,
  &.drag-over {
    border-color: rgba(255, 255, 255, 1);
  }
}

.cptm-placeholder-label {
  margin: 0;
  text-align: center;
  margin-bottom: 0;
  text-align: center;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 0;
  color: rgba(#fff, 0.4);
  font-size: 13px;
  &.hide {
    display: none;
  }
}
.cptm-listing-card-preview-footer {
  .cptm-placeholder-label {
    color: #868eae;
  }
}

.cptm-card-preview-widget {
  max-width: 450px;
  margin: 0 auto;
  border-radius: 10px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
  background-color: #fff;
}

.cptm-listing-card-preview-header {
  display: block;
}

.cptm-card-preview-thumbnail {
  position: relative;
  height: 280px;
  background: $dark-3;
  border-radius: 5px 5px 0 0;
}

.cptm-card-preview-thumbnail-overlay {
  position: relative;
  height: 100%;
}

.cptm-card-preview-thumbnail-bg {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  font-size: 72px;
  color: $dark-2;
}

.cptm-card-preview-thumbnail-bg span {
  color: rgba(#fff, 0.1);
}

.cptm-card-preview-top-left {
  width: calc(50% - 30px);
  position: absolute;
  top: 20px;
  left: 20px;
}

.cptm-card-preview-top-left-placeholder {
  display: block;
}

.cptm-card-preview-top-rignt {
  position: absolute;
  right: 20px;
  top: 20px;
  width: calc(50% - 30px);
}

.cptm-card-preview-top-rignt-placeholder {
  text-align: right;
}

.cptm-card-preview-bottom-left {
  position: absolute;
  width: calc(50% - 30px);
  bottom: 53px;
  left: 20px;
}

.cptm-card-preview-bottom-left-placeholder {
  display: block;
}

.cptm-card-preview-bottom-right {
  position: absolute;
  bottom: 53px;
  right: 20px;
  width: calc(50% - 30px);
}

.cptm-card-preview-bottom-right-placeholder {
  display: block;
}

.cptm-listing-card-preview-body {
  display: block;
  padding: 35px 20px 20px;
  position: relative;
}

.cptm-listing-card-author-avatar {
  z-index: 99;
  position: absolute;
  left: 50%;
  top: 0;
  width: calc(100% - 40px);
  transform: translate(-50%, -50%);
  .cptm-placeholder-blcok {
    border-color: rgba(#c6d0dc, 0.5);
    padding: 0;
    min-height: auto;
    height: 51px;
    .cptm-placeholder-author-thumb {
      margin: 0;
      img {
        width: 40px;
        height: 40px;
        border-radius: 50%;
        object-fit: cover;
        background: #c6d0dc;
        border: 4px solid #fff;
      }
    }
  }
  .cptm-widget-insert-area {
    top: 0;
    bottom: auto;
    transform: translate(-50%, -50%);
    .cptm-widget-insert-link {
      background: #fff;
      color: #5a5f7d;
    }
  }
}

.cptm-listing-card-preview-body-placeholder {
  display: block;
  min-height: 206px;
  border-color: #e3e6ef;
  &:hover {
    border-color: #717171;
  }
  .cptm-placeholder-label {
    color: #a1a8c6;
  }
}

.cptm-listing-card-preview-footer {
  display: flex;
  flex-wrap: wrap;
  padding: 20px;
  background-color: #f4f5f7;
  border-radius: 0 0 5px 5px;
}

.cptm-listing-card-preview-footer-left-placeholder {
  flex-grow: 1;
  margin-right: 5px;
  border-color: #c6d0dc;
  &:hover {
    border-color: #1e1e1e;
  }
}

.cptm-listing-card-preview-footer-right-placeholder {
  flex-grow: 1;
  border-color: #c6d0dc;
  &:hover {
    border-color: #1e1e1e;
  }
}

.cptm-widgets-container {
  overflow: hidden;
  border: 1px solid rgba(0, 0, 0, 0.1);
  background-color: #fff;
}
.cptm-widgets-header {
  display: block;
}

.cptm-widget-nav {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
}
.cptm-widget-nav-item {
  display: inline-block;
  margin: 0;
  padding: 12px 10px;
  cursor: pointer;
  flex-basis: calc(100% / 3);
  flex-grow: 1;
  text-align: center;
  box-sizing: border-box;
  cursor: pointer;
  color: #8a8a8a;
  border-right: 1px solid #e3e1e1;
  background-color: #f2f2f2;

  &:last-child {
    border-right: none;
  }

  &:hover {
    color: #2b2b2b;
  }

  &.active {
    font-weight: bold;
    color: #2b2b2b;
    background-color: #fff;
  }
}

.cptm-widgets-body {
  padding: 10px;
  max-height: 450px;
  overflow: hidden;
  overflow-y: auto;
}

.cptm-widgets-list {
  display: block;
  margin: 0;
}
.cptm-widgets-list-item {
  display: block;
}
.widget-group-title {
  margin: 0 0 5px;
  font-size: 16px;
  color: #bbb;
}

.cptm-widgets-sub-list {
  display: block;
  margin: 0;
}
.cptm-widgets-sub-list-item {
  display: block;
  padding: 10px 15px;
  background-color: #eee;
  border-radius: 5px;
  margin-bottom: 10px;
  cursor: move;
}

.widget-icon {
  display: inline-block;
  margin-right: 5px;
}
.widget-label {
  display: inline-block;
}

// Form Fields
.cptm-form-group {
  display: block;
  margin-bottom: 20px;
  label {
    display: block;
    font-size: 14px;
    font-weight: 500;
    color: $dark-3;
    margin-bottom: 9px;
  }
  .cptm-form-control {
    max-width: 100%;
  }

  &.cpt-has-error .cptm-form-control {
    border: 1px solid rgb(192, 51, 51);
  }
}

.cptm-form-group-feedback {
  display: block;
}

.cptm-form-alert {
  padding: 10px 0;
  transition: all ease-in-out 300ms;

  &.cptm-error {
    color: #c82424;
  }
}

.cptm-input-toggle-wrap {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  label {
    padding-right: 10px;
    flex-grow: 1;
    margin-bottom: 0;
  }
}

.cptm-input-toggle {
  display: inline-block;
  position: relative;
  width: 50px;
  height: 25px;
  background-color: #d9d9d9;
  border-radius: 30px;
  transition: all ease-in-out 300ms;
  cursor: pointer;

  &::after {
    content: "";
    display: inline-block;
    width: 30%;
    height: calc(100% - 10px);
    background-color: #fff;
    border-radius: 50%;
    position: absolute;
    top: 0;
    left: 0;
    margin: 5px;
    transition: all ease-in-out 300ms;
  }

  &.active {
    background-color: $primary;
    &::after {
      left: 100%;
      transform: translateX(calc(-100% - 10px));
    }
  }
}

.cptm-multi-option-group {
  display: block;
  margin-bottom: 20px;

  .cptm-btn {
    margin: 0;
  }
}
.cptm-multi-option-label {
  display: block;
}
.cptm-multi-option-group-section {
  display: flex;
  flex-wrap: wrap;
  margin: -8px;
  .cptm-form-group {
    margin: 0 8px 20px;
    flex: 1;
    .cptm-form-control {
      width: 100%;
    }
    &.cpt-has-error {
      position: relative;
    }
  }
  p {
    margin: 28px 8px 20px;
  }
}

.cptm-form-control,
select.cptm-form-control,
input[type="date"].cptm-form-control,
input[type="datetime-local"].cptm-form-control,
input[type="datetime"].cptm-form-control,
input[type="email"].cptm-form-control,
input[type="month"].cptm-form-control,
input[type="number"].cptm-form-control,
input[type="password"].cptm-form-control,
input[type="search"].cptm-form-control,
input[type="tel"].cptm-form-control,
input[type="text"].cptm-form-control,
input[type="time"].cptm-form-control,
input[type="url"].cptm-form-control,
input[type="week"].cptm-form-control input[type="text"].cptm-form-control {
  padding: 11px 20px;
  font-size: 13px;
  color: #5a5f7d;
  text-align: left;
  border: 0 none;
  border-radius: 4px;
  box-shadow: none;
  width: 100%;
  font-weight: 400;
  margin: 0;
  line-height: 18px;
  height: auto;
  min-height: 30px;
  background: #f4f5f7;
  &:hover {
    color: #23282d;
  }
}
